@import "ScssHelpers/helpers-all.scss";

.location_item {
	display: flex;
	flex-direction: column;
	align-items: flex-start;
	justify-content: flex-start;

	width: 100%;
	height: 100%;

	background-color: $gray_3;

	@include bp($point_2, min) {
		padding: 3.2rem 4rem 4rem;
	}

	@include bp($point_3) {
		padding: 1.4rem 1.6rem 2.4rem 2rem;
	}
}

.location_item__title {
	@include h4;

	.wher_buy_info & {
		min-height: 2em;
	}

	@include bp($point_2, min) {
		line-height: 1.14;
		letter-spacing: -0.014em;

		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}

	@include bp($point_3) {
		line-height: 1;
		letter-spacing: -0.022em;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}

		.get_in_touch & br {
			display: none;
		}
	}
}

.location_item__list {
	width: 100%;

	@include bp($point_3) {
		margin-top: auto;
	}
}

.location_item__item {
	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 1.8rem;
		}
		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}

	@include bp($point_2, min) {
		display: flex;
		align-items: flex-start;
		justify-content: flex-start;
	}
	@include bp($point_3) {
		.get_in_touch & {
			display: flex;
		}
	}
}

.location_item__item_heading {
	color: $gray_2;
	font-weight: 500;
	letter-spacing: 0.12em;
	text-transform: uppercase;

	@include bp($point_2, min) {
		flex-shrink: 0;
		width: 30%;

		font-size: 1.6rem;
		line-height: 1.72;

		&:not(:last-child) {
			margin-right: 1.8rem;
		}
	}

	@include bp($point_3) {
		font-size: 1.2rem;
		line-height: 1.6;

		.get_in_touch & {
			&:not(:last-child) {
				margin-right: 1.8rem;
			}
		}
	}
}

.location_item__item_link {
	@at-root {
		a#{&} {
			transition: color 0.3s;

			&:hover,
			&:focus {
				text-decoration: none;
				color: $gray_2;
			}
		}
	}

	.get_in_touch & {
		margin-left: auto;
		width: 17.6rem;
	}

	@include bp($point_2, min) {
		@include text;

		padding-top: 2px;
		line-height: 1.25;
		color: $black;
	}

	@include bp($point_3) {
		font-size: 1.3rem;
		line-height: 1.38;
		letter-spacing: -0.02em;
		color: rgba(0, 0, 0, 0.8);
	}
}
