@import "ScssHelpers/helpers-all.scss";

.location_list {
	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-bottom: 5.6rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.location_list__items {
	@include flex_block(row, wrap, center, stretch);

	@include bp($point_4, min) {
		margin: 0 -2.4rem -3.2rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 -1.6rem -2rem 0;
	}
}

.location_list__item {
	min-height: 100%;

	@include bp($point_4, min) {
		width: 33.333%;
		padding: 0 2.4rem 3.2rem 0;
	}

	@include bp($point_4 - 1) {
		width: 50%;
		padding: 0 1.6rem 2rem 0;
	}
}
