@import "ScssHelpers/helpers-all.scss";
//@import "map";

.location_map {
	&:not(:last-child) {
		margin-bottom: 5.4rem;
	}
}

.location_map__head {
	@include flex_block(row, wrap, space-between, center);

	&:not(:last-child) {
		@include bp($point_4, min) {
			margin-bottom: 3rem;
		}

		@include bp($point_4 - 1) {
			margin-bottom: 2rem;
		}
	}
}

.location_map__head_title {
	@include h2;
	letter-spacing: -0.014em;

	@include bp($point_4, min) {
		padding-right: 3rem;
	}

	@include bp($point_4 - 1) {
		width: 100%;

		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}
}

.location_map__head_form {
	@include flex_block(row, nowrap, flex-start, center);

	@include bp($point_4 - 1) {
		flex: auto;
	}
}

.location_map__head_search {
	@include bp($point_4 - 1) {
		width: 100%;
	}
}
